import { useState, useEffect } from 'react';
import { collection, getDocs, addDoc, updateDoc, deleteDoc, doc, query, where, orderBy, writeBatch } from 'firebase/firestore';
import { db } from '../../firebase';
import { useNotification } from './NotificationSystem';
import { 
  FiPlus, FiEdit2, FiTrash2, FiCheck, FiClock, FiCalendar, 
  FiFlag, FiUser, FiFilter, FiRefreshCw, FiCheckCircle, FiList,
  FiTarget, FiTrendingUp, FiActivity, FiSearch, FiX, FiArrowUp, FiArrowDown,
  FiPlay, FiPause, FiRotateCcw, FiBarChart2, FiSettings, FiChevronRight
} from 'react-icons/fi';

export default function IndustrialTaskManager() {
  const { showSuccess, showError } = useNotification();
  const [tasks, setTasks] = useState([]);
  const [loading, setLoading] = useState(true);
  const [showAddForm, setShowAddForm] = useState(false);
  const [editingTask, setEditingTask] = useState(null);
  const [filter, setFilter] = useState('all');
  const [selectedDate, setSelectedDate] = useState(new Date().toISOString().split('T')[0]);
  const [activeSequence, setActiveSequence] = useState(null);
  const [currentStepIndex, setCurrentStepIndex] = useState(0);
  
  const [formData, setFormData] = useState({
    title: '',
    description: '',
    priority: 'medium',
    assignedTo: '',
    dueDate: new Date().toISOString().split('T')[0],
    category: 'production',
    sequence: [{ step: '', completed: false }],
    estimatedTime: 0,
    department: 'manufacturing',
    equipment: '',
    safetyNotes: ''
  });

  useEffect(() => {
    fetchTasks();
  }, [selectedDate]);

  const fetchTasks = async () => {
    try {
      setLoading(true);
      const startOfDay = new Date(selectedDate);
      startOfDay.setHours(0, 0, 0, 0);
      
      const endOfDay = new Date(selectedDate);
      endOfDay.setHours(23, 59, 59, 999);

      const tasksQuery = query(
        collection(db, 'industrialTasks'),
        where('dueDate', '>=', startOfDay),
        where('dueDate', '<=', endOfDay),
        orderBy('dueDate', 'asc')
      );
      
      const tasksSnapshot = await getDocs(tasksQuery);
      const tasksData = [];
      tasksSnapshot.forEach((doc) => {
        tasksData.push({ id: doc.id, ...doc.data() });
      });

      // Secondary sort by priority on client to avoid composite index
      const prioWeight = { high: 3, medium: 2, low: 1 };
      tasksData.sort((a, b) => {
        const da = a.dueDate?.toDate ? a.dueDate.toDate() : new Date(a.dueDate);
        const db = b.dueDate?.toDate ? b.dueDate.toDate() : new Date(b.dueDate);
        if (da.getTime() !== db.getTime()) return da - db;
        const pa = prioWeight[a.priority] || 0;
        const pb = prioWeight[b.priority] || 0;
        return pb - pa; // high before low
      });

      setTasks(tasksData);
    } catch (error) {
      console.error('Error fetching tasks:', error);
      showError('Failed to load tasks');
    } finally {
      setLoading(false);
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!formData.title.trim()) {
      showError('Please enter a title');
      return;
    }

    try {
      const taskData = {
        ...formData,
        dueDate: new Date(formData.dueDate),
        completed: false,
        createdAt: new Date(),
        updatedAt: new Date(),
        progress: 0,
        status: 'pending'
      };

      if (editingTask) {
        await updateDoc(doc(db, 'industrialTasks', editingTask.id), {
          ...taskData,
          createdAt: editingTask.createdAt
        });
        showSuccess('Task updated successfully');
      } else {
        await addDoc(collection(db, 'industrialTasks'), taskData);
        showSuccess('Task added successfully');
      }
      
      resetForm();
      fetchTasks();
    } catch (error) {
      console.error('Error saving task:', error);
      showError('Failed to save task');
    }
  };

  const toggleComplete = async (taskId, completed) => {
    try {
      await updateDoc(doc(db, 'industrialTasks', taskId), {
        completed: !completed,
        updatedAt: new Date(),
        status: !completed ? 'completed' : 'pending'
      });
      
      setTasks(prev => prev.map(task => 
        task.id === taskId ? { ...task, completed: !completed, status: !completed ? 'completed' : 'pending' } : task
      ));
      
      showSuccess(`Task marked as ${!completed ? 'completed' : 'pending'}`);
    } catch (error) {
      console.error('Error updating task:', error);
      showError('Failed to update task');
    }
  };

  const updateProgress = async (taskId, progress) => {
    try {
      await updateDoc(doc(db, 'industrialTasks', taskId), {
        progress,
        updatedAt: new Date(),
        status: progress === 100 ? 'completed' : 'in-progress'
      });
      
      setTasks(prev => prev.map(task => 
        task.id === taskId ? { ...task, progress, status: progress === 100 ? 'completed' : 'in-progress' } : task
      ));
      
      showSuccess(`Progress updated to ${progress}%`);
    } catch (error) {
      console.error('Error updating progress:', error);
      showError('Failed to update progress');
    }
  };

  const deleteTask = async (taskId) => {
    if (window.confirm('Are you sure you want to delete this task?')) {
      try {
        await deleteDoc(doc(db, 'industrialTasks', taskId));
        setTasks(prev => prev.filter(task => task.id !== taskId));
        showSuccess('Task deleted successfully');
      } catch (error) {
        console.error('Error deleting task:', error);
        showError('Failed to delete task');
      }
    }
  };

  const editTask = (task) => {
    setFormData({
      title: task.title,
      description: task.description || '',
      priority: task.priority,
      assignedTo: task.assignedTo || '',
      dueDate: task.dueDate?.toDate?.()?.toISOString().split('T')[0] || selectedDate,
      category: task.category || 'production',
      sequence: task.sequence || [{ step: '', completed: false }],
      estimatedTime: task.estimatedTime || 0,
      department: task.department || 'manufacturing',
      equipment: task.equipment || '',
      safetyNotes: task.safetyNotes || ''
    });
    setEditingTask(task);
    setShowAddForm(true);
  };

  const resetForm = () => {
    setFormData({
      title: '',
      description: '',
      priority: 'medium',
      assignedTo: '',
      dueDate: selectedDate,
      category: 'production',
      sequence: [{ step: '', completed: false }],
      estimatedTime: 0,
      department: 'manufacturing',
      equipment: '',
      safetyNotes: ''
    });
    setShowAddForm(false);
    setEditingTask(null);
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleSequenceChange = (index, field, value) => {
    const updatedSequence = [...formData.sequence];
    updatedSequence[index][field] = value;
    setFormData(prev => ({
      ...prev,
      sequence: updatedSequence
    }));
  };

  const addSequenceStep = () => {
    setFormData(prev => ({
      ...prev,
      sequence: [...prev.sequence, { step: '', completed: false }]
    }));
  };

  const removeSequenceStep = (index) => {
    if (formData.sequence.length > 1) {
      const updatedSequence = [...formData.sequence];
      updatedSequence.splice(index, 1);
      setFormData(prev => ({
        ...prev,
        sequence: updatedSequence
      }));
    }
  };

  const moveSequenceStep = (index, direction) => {
    if ((direction === 'up' && index === 0) || 
        (direction === 'down' && index === formData.sequence.length - 1)) {
      return;
    }
    
    const updatedSequence = [...formData.sequence];
    const targetIndex = direction === 'up' ? index - 1 : index + 1;
    [updatedSequence[index], updatedSequence[targetIndex]] = 
    [updatedSequence[targetIndex], updatedSequence[index]];
    
    setFormData(prev => ({
      ...prev,
      sequence: updatedSequence
    }));
  };

  const startSequence = (task) => {
    setActiveSequence(task);
    setCurrentStepIndex(0);
  };

  const completeStep = async (taskId, stepIndex) => {
    try {
      const task = tasks.find(t => t.id === taskId);
      const updatedSequence = [...task.sequence];
      updatedSequence[stepIndex].completed = true;
      
      const progress = Math.round(((stepIndex + 1) / task.sequence.length) * 100);
      
      const batch = writeBatch(db);
      const taskRef = doc(db, 'industrialTasks', taskId);
      
      batch.update(taskRef, {
        sequence: updatedSequence,
        progress,
        updatedAt: new Date(),
        status: progress === 100 ? 'completed' : 'in-progress'
      });
      
      await batch.commit();
      
      setTasks(prev => prev.map(t => 
        t.id === taskId ? { 
          ...t, 
          sequence: updatedSequence, 
          progress,
          status: progress === 100 ? 'completed' : 'in-progress'
        } : t
      ));
      
      if (stepIndex < task.sequence.length - 1) {
        setCurrentStepIndex(stepIndex + 1);
      } else {
        setActiveSequence(null);
        showSuccess('Sequence completed!');
      }
    } catch (error) {
      console.error('Error completing step:', error);
      showError('Failed to complete step');
    }
  };

  const getPriorityColor = (priority) => {
    switch (priority) {
      case 'high':
        return 'bg-red-100 text-red-800 border-red-300';
      case 'medium':
        return 'bg-yellow-100 text-yellow-800 border-yellow-300';
      case 'low':
        return 'bg-green-100 text-green-800 border-green-300';
      default:
        return 'bg-gray-100 text-gray-800 border-gray-300';
    }
  };

  const getCategoryColor = (category) => {
    switch (category) {
      case 'production':
        return 'bg-blue-100 text-blue-800 border-blue-300';
      case 'maintenance':
        return 'bg-purple-100 text-purple-800 border-purple-300';
      case 'safety':
        return 'bg-red-100 text-red-800 border-red-300';
      case 'quality':
        return 'bg-indigo-100 text-indigo-800 border-indigo-300';
      case 'logistics':
        return 'bg-orange-100 text-orange-800 border-orange-300';
      default:
        return 'bg-gray-100 text-gray-800 border-gray-300';
    }
  };

  const getStatusColor = (status) => {
    switch (status) {
      case 'completed':
        return 'bg-green-100 text-green-800 border-green-300';
      case 'in-progress':
        return 'bg-blue-100 text-blue-800 border-blue-300';
      case 'pending':
        return 'bg-yellow-100 text-yellow-800 border-yellow-300';
      case 'on-hold':
        return 'bg-red-100 text-red-800 border-red-300';
      default:
        return 'bg-gray-100 text-gray-800 border-gray-300';
    }
  };

  const getDepartmentColor = (department) => {
    switch (department) {
      case 'manufacturing':
        return 'bg-blue-50 text-blue-700';
      case 'assembly':
        return 'bg-green-50 text-green-700';
      case 'packaging':
        return 'bg-yellow-50 text-yellow-700';
      case 'shipping':
        return 'bg-orange-50 text-orange-700';
      case 'quality-control':
        return 'bg-purple-50 text-purple-700';
      default:
        return 'bg-gray-50 text-gray-700';
    }
  };

  const filteredTasks = tasks.filter(task => {
    if (filter === 'completed') return task.completed;
    if (filter === 'pending') return !task.completed;
    if (filter === 'in-progress') return task.status === 'in-progress';
    return true;
  });

  const stats = {
    total: tasks.length,
    completed: tasks.filter(t => t.completed).length,
    pending: tasks.filter(t => !t.completed).length,
    inProgress: tasks.filter(t => t.status === 'in-progress').length,
    highPriority: tasks.filter(t => t.priority === 'high' && !t.completed).length
  };

  return (
    <div className="space-y-6 bg-gray-50 min-h-screen p-4">
      {/* Header */}
      <div className="bg-white p-6 rounded-lg shadow border border-gray-200">
        <div className="flex justify-between items-center">
          <div className="flex items-center space-x-4">
            <div className="w-12 h-12 bg-gradient-to-br from-blue-600 to-blue-800 rounded-lg flex items-center justify-center">
              <FiList className="text-white" size={24} />
            </div>
            <div>
              <h1 className="text-2xl font-bold text-gray-900">Industrial Task Manager</h1>
              <p className="text-gray-600">Manage production tasks and workflows</p>
            </div>
          </div>
          <div className="flex space-x-3">
            <button
              onClick={fetchTasks}
              className="inline-flex items-center px-4 py-2 bg-gray-600 text-white font-medium rounded-md hover:bg-gray-700 transition-colors"
            >
              <FiRefreshCw className="mr-2" size={16} />
              Refresh
            </button>
            <button
              onClick={() => setShowAddForm(true)}
              className="inline-flex items-center px-4 py-2 bg-blue-600 text-white font-medium rounded-md hover:bg-blue-700 transition-colors"
            >
              <FiPlus className="mr-2" size={16} />
              New Task
            </button>
          </div>
        </div>
      </div>

      {/* Stats Overview */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-5 gap-4">
        <div className="bg-white p-4 rounded-lg shadow border border-gray-200">
          <div className="flex items-center">
            <div className="w-10 h-10 bg-blue-100 rounded-lg flex items-center justify-center mr-3">
              <FiTarget className="text-blue-600" size={20} />
            </div>
            <div>
              <p className="text-sm text-gray-600">Total Tasks</p>
              <p className="text-xl font-bold text-gray-900">{stats.total}</p>
            </div>
          </div>
        </div>

        <div className="bg-white p-4 rounded-lg shadow border border-gray-200">
          <div className="flex items-center">
            <div className="w-10 h-10 bg-green-100 rounded-lg flex items-center justify-center mr-3">
              <FiCheckCircle className="text-green-600" size={20} />
            </div>
            <div>
              <p className="text-sm text-gray-600">Completed</p>
              <p className="text-xl font-bold text-gray-900">{stats.completed}</p>
            </div>
          </div>
        </div>

        <div className="bg-white p-4 rounded-lg shadow border border-gray-200">
          <div className="flex items-center">
            <div className="w-10 h-10 bg-yellow-100 rounded-lg flex items-center justify-center mr-3">
              <FiClock className="text-yellow-600" size={20} />
            </div>
            <div>
              <p className="text-sm text-gray-600">In Progress</p>
              <p className="text-xl font-bold text-gray-900">{stats.inProgress}</p>
            </div>
          </div>
        </div>

        <div className="bg-white p-4 rounded-lg shadow border border-gray-200">
          <div className="flex items-center">
            <div className="w-10 h-10 bg-red-100 rounded-lg flex items-center justify-center mr-3">
              <FiFlag className="text-red-600" size={20} />
            </div>
            <div>
              <p className="text-sm text-gray-600">High Priority</p>
              <p className="text-xl font-bold text-gray-900">{stats.highPriority}</p>
            </div>
          </div>
        </div>

        <div className="bg-white p-4 rounded-lg shadow border border-gray-200">
          <div className="flex items-center">
            <div className="w-10 h-10 bg-purple-100 rounded-lg flex items-center justify-center mr-3">
              <FiCalendar className="text-purple-600" size={20} />
            </div>
            <div>
              <p className="text-sm text-gray-600">Date</p>
              <input
                type="date"
                value={selectedDate}
                onChange={(e) => setSelectedDate(e.target.value)}
                className="text-sm font-medium text-gray-900 bg-transparent border-none p-0"
              />
            </div>
          </div>
        </div>
      </div>

      {/* Filters */}
      <div className="bg-white p-4 rounded-lg shadow border border-gray-200">
        <div className="flex flex-wrap items-center gap-2">
          <span className="text-sm font-medium text-gray-700">Filter:</span>
          {['all', 'pending', 'in-progress', 'completed'].map((filterType) => (
            <button
              key={filterType}
              onClick={() => setFilter(filterType)}
              className={`px-3 py-1 rounded-md text-sm font-medium transition-colors ${
                filter === filterType
                  ? 'bg-blue-600 text-white'
                  : 'bg-gray-200 text-gray-700 hover:bg-gray-300'
              }`}
            >
              {filterType.charAt(0).toUpperCase() + filterType.slice(1)}
            </button>
          ))}
        </div>
      </div>

      {/* Task List */}
      <div className="bg-white rounded-lg shadow border border-gray-200">
        <div className="p-4 border-b border-gray-200">
          <h2 className="text-lg font-semibold text-gray-900">
            Tasks for {new Date(selectedDate).toLocaleDateString()}
          </h2>
        </div>
        
        <div className="p-4">
          {loading ? (
            <div className="flex justify-center items-center h-32">
              <div className="animate-spin rounded-full h-8 w-8 border-t-2 border-b-2 border-blue-500"></div>
            </div>
          ) : filteredTasks.length > 0 ? (
            <div className="space-y-4">
              {filteredTasks.map((task) => (
                <div
                  key={task.id}
                  className={`p-4 border rounded-lg ${
                    task.completed ? 'bg-gray-50 border-gray-200' : 'bg-white border-gray-300'
                  }`}
                >
                  <div className="flex items-start justify-between">
                    <div className="flex-1">
                      <div className="flex items-center justify-between mb-2">
                        <h3 className={`font-medium ${task.completed ? 'text-gray-500 line-through' : 'text-gray-900'}`}>
                          {task.title}
                        </h3>
                        <div className="flex items-center space-x-2">
                          <span className={`inline-flex items-center px-2 py-1 rounded-md text-xs font-medium ${getPriorityColor(task.priority)}`}>
                            <FiFlag className="mr-1" size={10} />
                            {task.priority}
                          </span>
                          <span className={`inline-flex items-center px-2 py-1 rounded-md text-xs font-medium ${getStatusColor(task.status)}`}>
                            {task.status}
                          </span>
                        </div>
                      </div>
                      
                      {task.description && (
                        <p className={`text-sm mb-3 ${task.completed ? 'text-gray-400' : 'text-gray-600'}`}>
                          {task.description}
                        </p>
                      )}
                      
                      <div className="flex items-center flex-wrap gap-2 mb-3">
                        <span className={`inline-flex items-center px-2 py-1 rounded-md text-xs font-medium ${getCategoryColor(task.category)}`}>
                          {task.category}
                        </span>
                        <span className={`inline-flex items-center px-2 py-1 rounded-md text-xs font-medium ${getDepartmentColor(task.department)}`}>
                          {task.department}
                        </span>
                        {task.assignedTo && (
                          <span className="inline-flex items-center px-2 py-1 bg-blue-100 text-blue-700 rounded-md text-xs font-medium">
                            <FiUser className="mr-1" size={10} />
                            {task.assignedTo}
                          </span>
                        )}
                        {task.equipment && (
                          <span className="inline-flex items-center px-2 py-1 bg-gray-100 text-gray-700 rounded-md text-xs font-medium">
                            üõ†Ô∏è {task.equipment}
                          </span>
                        )}
                        {task.estimatedTime > 0 && (
                          <span className="inline-flex items-center px-2 py-1 bg-gray-100 text-gray-700 rounded-md text-xs font-medium">
                            <FiClock className="mr-1" size={10} />
                            {task.estimatedTime} min
                          </span>
                        )}
                      </div>
                      
                      {/* Progress Bar */}
                      <div className="mb-3">
                        <div className="flex justify-between text-xs text-gray-600 mb-1">
                          <span>Progress</span>
                          <span>{task.progress}%</span>
                        </div>
                        <div className="w-full bg-gray-200 rounded-full h-2">
                          <div 
                            className="bg-blue-600 h-2 rounded-full" 
                            style={{ width: `${task.progress}%` }}
                          ></div>
                        </div>
                      </div>
                      
                      {/* Sequence Steps */}
                      {task.sequence && task.sequence.length > 0 && (
                        <div className="mb-3">
                          <div className="flex items-center justify-between mb-2">
                            <span className="text-sm font-medium text-gray-700">Sequence Steps</span>
                            {!task.completed && (
                              <button
                                onClick={() => startSequence(task)}
                                className="inline-flex items-center px-2 py-1 bg-blue-100 text-blue-700 rounded-md text-xs font-medium hover:bg-blue-200 transition-colors"
                              >
                                <FiPlay className="mr-1" size={10} />
                                Start Sequence
                              </button>
                            )}
                          </div>
                          <div className="space-y-1">
                            {task.sequence.map((step, index) => (
                              <div key={index} className="flex items-center">
                                <div className={`w-5 h-5 rounded-full flex items-center justify-center mr-2 ${
                                  step.completed ? 'bg-green-500' : 'bg-gray-300'
                                }`}>
                                  {step.completed ? (
                                    <FiCheck className="text-white" size={12} />
                                  ) : (
                                    <span className="text-xs text-white">{index + 1}</span>
                                  )}
                                </div>
                                <span className={`text-sm ${step.completed ? 'text-gray-500 line-through' : 'text-gray-700'}`}>
                                  {step.step}
                                </span>
                              </div>
                            ))}
                          </div>
                        </div>
                      )}
                      
                      {task.safetyNotes && (
                        <div className="mb-3 p-2 bg-yellow-50 border border-yellow-200 rounded-md">
                          <p className="text-xs text-yellow-800">
                            <span className="font-medium">Safety Notes:</span> {task.safetyNotes}
                          </p>
                        </div>
                      )}
                    </div>
                    
                    <div className="flex items-center space-x-2 ml-4">
                      {!task.completed && (
                        <button
                          onClick={() => editTask(task)}
                          className="p-2 text-gray-400 hover:text-blue-600 hover:bg-blue-50 rounded-md transition-colors"
                          title="Edit Task"
                        >
                          <FiEdit2 size={16} />
                        </button>
                      )}
                      <button
                        onClick={() => deleteTask(task.id)}
                        className="p-2 text-gray-400 hover:text-red-600 hover:bg-red-50 rounded-md transition-colors"
                        title="Delete Task"
                      >
                        <FiTrash2 size={16} />
                      </button>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          ) : (
            <div className="text-center py-8">
              <div className="w-16 h-16 bg-gray-200 rounded-lg flex items-center justify-center mx-auto mb-4">
                <FiList className="text-gray-500" size={24} />
              </div>
              <h3 className="text-lg font-medium text-gray-900 mb-2">No tasks for this date</h3>
              <p className="text-sm text-gray-500 mb-4">Create a new task to get started</p>
              <button
                onClick={() => setShowAddForm(true)}
                className="inline-flex items-center px-4 py-2 bg-blue-600 text-white font-medium rounded-md hover:bg-blue-700 transition-colors"
              >
                <FiPlus className="mr-2" size={16} />
                Add Task
              </button>
            </div>
          )}
        </div>
      </div>

      {/* Sequence Runner Modal */}
      {activeSequence && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-lg shadow-lg p-6 w-full max-w-md">
            <div className="flex items-center justify-between mb-4">
              <h3 className="text-lg font-semibold text-gray-900">Execute Sequence</h3>
              <button
                onClick={() => setActiveSequence(null)}
                className="text-gray-400 hover:text-gray-600"
              >
                <FiX size={20} />
              </button>
            </div>
            
            <div className="mb-4">
              <h4 className="font-medium text-gray-900 mb-2">{activeSequence.title}</h4>
              <p className="text-sm text-gray-600">{currentStepIndex + 1} of {activeSequence.sequence.length} steps</p>
            </div>
            
            <div className="mb-6">
              <div className="p-4 bg-blue-50 border border-blue-200 rounded-lg">
                <p className="text-sm font-medium text-blue-800">
                  Step {currentStepIndex + 1}: {activeSequence.sequence[currentStepIndex].step}
                </p>
              </div>
            </div>
            
            <div className="flex justify-between">
              <button
                onClick={() => setActiveSequence(null)}
                className="px-4 py-2 text-gray-700 bg-gray-200 font-medium rounded-md hover:bg-gray-300 transition-colors"
              >
                Cancel
              </button>
              <button
                onClick={() => completeStep(activeSequence.id, currentStepIndex)}
                className="px-4 py-2 bg-blue-600 text-white font-medium rounded-md hover:bg-blue-700 transition-colors"
              >
                {currentStepIndex < activeSequence.sequence.length - 1 ? 'Next Step' : 'Complete Sequence'}
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Add/Edit Form Modal */}
      {(showAddForm || editingTask) && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-lg shadow-lg p-6 w-full max-w-2xl max-h-[90vh] overflow-y-auto">
            <div className="flex items-center justify-between mb-6">
              <h3 className="text-xl font-semibold text-gray-900">
                {editingTask ? 'Edit Task' : 'Add New Task'}
              </h3>
              <button
                onClick={resetForm}
                className="text-gray-400 hover:text-gray-600"
              >
                <FiX size={24} />
              </button>
            </div>
            
            <form onSubmit={handleSubmit} className="space-y-4">
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    Title *
                  </label>
                  <input
                    type="text"
                    name="title"
                    value={formData.title}
                    onChange={handleInputChange}
                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-blue-500 focus:border-blue-500"
                    placeholder="Enter task title"
                    required
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    Category
                  </label>
                  <select
                    name="category"
                    value={formData.category}
                    onChange={handleInputChange}
                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-blue-500 focus:border-blue-500"
                  >
                    <option value="production">Production</option>
                    <option value="maintenance">Maintenance</option>
                    <option value="safety">Safety</option>
                    <option value="quality">Quality Control</option>
                    <option value="logistics">Logistics</option>
                  </select>
                </div>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Description
                </label>
                <textarea
                  name="description"
                  value={formData.description}
                  onChange={handleInputChange}
                  rows={3}
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-blue-500 focus:border-blue-500"
                  placeholder="Enter task description"
                />
              </div>

              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    Priority
                  </label>
                  <select
                    name="priority"
                    value={formData.priority}
                    onChange={handleInputChange}
                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-blue-500 focus:border-blue-500"
                  >
                    <option value="low">Low</option>
                    <option value="medium">Medium</option>
                    <option value="high">High</option>
                  </select>
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    Department
                  </label>
                  <select
                    name="department"
                    value={formData.department}
                    onChange={handleInputChange}
                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-blue-500 focus:border-blue-500"
                  >
                    <option value="manufacturing">Manufacturing</option>
                    <option value="assembly">Assembly</option>
                    <option value="packaging">Packaging</option>
                    <option value="shipping">Shipping</option>
                    <option value="quality-control">Quality Control</option>
                  </select>
                </div>
              </div>

              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    Assigned To
                  </label>
                  <input
                    type="text"
                    name="assignedTo"
                    value={formData.assignedTo}
                    onChange={handleInputChange}
                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-blue-500 focus:border-blue-500"
                    placeholder="Assign to operator"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    Due Date
                  </label>
                  <input
                    type="date"
                    name="dueDate"
                    value={formData.dueDate}
                    onChange={handleInputChange}
                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-blue-500 focus:border-blue-500"
                  />
                </div>
              </div>

              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    Equipment
                  </label>
                  <input
                    type="text"
                    name="equipment"
                    value={formData.equipment}
                    onChange={handleInputChange}
                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-blue-500 focus:border-blue-500"
                    placeholder="Machine or tool required"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    Estimated Time (minutes)
                  </label>
                  <input
                    type="number"
                    name="estimatedTime"
                    value={formData.estimatedTime}
                    onChange={handleInputChange}
                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-blue-500 focus:border-blue-500"
                    min="0"
                  />
                </div>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Safety Notes
                </label>
                <textarea
                  name="safetyNotes"
                  value={formData.safetyNotes}
                  onChange={handleInputChange}
                  rows={2}
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-blue-500 focus:border-blue-500"
                  placeholder="Important safety precautions"
                />
              </div>

              <div>
                <div className="flex items-center justify-between mb-2">
                  <label className="block text-sm font-medium text-gray-700">
                    Sequence Steps
                  </label>
                  <button
                    type="button"
                    onClick={addSequenceStep}
                    className="text-sm text-blue-600 hover:text-blue-800"
                  >
                    + Add Step
                  </button>
                </div>
                
                <div className="space-y-2">
                  {formData.sequence.map((step, index) => (
                    <div key={index} className="flex items-center space-x-2">
                      <span className="text-sm text-gray-600 w-6">{index + 1}.</span>
                      <input
                        type="text"
                        value={step.step}
                        onChange={(e) => handleSequenceChange(index, 'step', e.target.value)}
                        className="flex-1 px-3 py-2 border border-gray-300 rounded-md focus:ring-blue-500 focus:border-blue-500"
                        placeholder="Describe this step"
                      />
                      <button
                        type="button"
                        onClick={() => moveSequenceStep(index, 'up')}
                        disabled={index === 0}
                        className={`p-2 rounded-md ${index === 0 ? 'text-gray-300' : 'text-gray-600 hover:bg-gray-100'}`}
                      >
                        <FiArrowUp size={16} />
                      </button>
                      <button
                        type="button"
                        onClick={() => moveSequenceStep(index, 'down')}
                        disabled={index === formData.sequence.length - 1}
                        className={`p-2 rounded-md ${index === formData.sequence.length - 1 ? 'text-gray-300' : 'text-gray-600 hover:bg-gray-100'}`}
                      >
                        <FiArrowDown size={16} />
                      </button>
                      <button
                        type="button"
                        onClick={() => removeSequenceStep(index)}
                        disabled={formData.sequence.length === 1}
                        className={`p-2 rounded-md ${formData.sequence.length === 1 ? 'text-gray-300' : 'text-red-600 hover:bg-red-100'}`}
                      >
                        <FiX size={16} />
                      </button>
                    </div>
                  ))}
                </div>
              </div>

              <div className="flex justify-end space-x-3 pt-4 border-t border-gray-200">
                <button
                  type="button"
                  onClick={resetForm}
                  className="px-4 py-2 text-gray-700 bg-gray-200 font-medium rounded-md hover:bg-gray-300 transition-colors"
                >
                  Cancel
                </button>
                <button
                  type="submit"
                  className="px-4 py-2 bg-blue-600 text-white font-medium rounded-md hover:bg-blue-700 transition-colors"
                >
                  {editingTask ? 'Update' : 'Add'} Task
                </button>
              </div>
            </form>
          </div>
        </div>
      )}
    </div>
  );
}